

.logged-in .main-navigation {
  top: 50px;
}

.main-navigation{
  display: flex;
  justify-content: space-between;
  padding-left:1.5rem;
  text-transform:uppercase;
  font-size:1rem;
  line-height:1.75rem;
  border-bottom: 1px solid $color_border-solid;
  position: fixed;
  width:100%;
  top:0;
  background:white;
  z-index: 900; 
 
      a{
        color:$color_menu-links;
      }

      .nav-logo {
          img {
             width: 50px;
              height: 33px;
              }
       }
  }    


 
  .main-nav {
   display: flex;
   align-items: center;
      li{
       padding:0 10px; 
      }
  }
   
fieldset{
  border:none;
  width: 100%;
  a.icon-search {
    display: initial ;
  }
}

.search-field{
  width:0px;
  border:none;
  transition: 500ms ease;
  opacity: 0;
  &.bar{
    opacity: 1;
    width:250px;
  }  
}


.search-submit{
  display:none;
}